.Main {
    padding: 20vh 5vh;
    width: 100%;
    font-size: 1.8rem;
    /* height: 100vh; */
}

.Main button {
    cursor: pointer;
    /* background-color: hsl(209, 23%, 22%); */
    /* color: white; */
    border: none;
    padding: 1rem 3rem;
    border-radius: 5px;
}

.Main button:first-child {
    margin: 0;
}

.MoreInfo {
    display: flex;
    flex-wrap: wrap;
    margin-top: 3rem;
    align-items: center;
    width: 100%;
    /* height: 100vh; */
}


.MoreInfo .left,
.MoreInfo .right {
    flex-basis: 50%;
    width: 100%;
}


.MoreInfo .left img {
    width: 100%;
    height: auto;
}

.MoreInfo .right {
    padding: 5rem;
}

.MoreInfo p {
    font-weight: 300;
    margin-top: 1rem;
}

.MoreInfo h1,
.MoreInfo span {
    font-weight: 600;
    margin-right: 1rem;
}

.MoreInfo .container {
    display: flex;
    align-items: center;
    width: 100%;
    /* justify-content: space-between; */
    /* flex-wrap: wrap; */
    font-size: 1.6rem;
}

.MoreInfo .right h1 {
    font-size: 3rem;
    margin-bottom: 3rem;
    font-weight: 800;
}

.MoreInfo .container .innerLeft,
.MoreInfo .container .innerRight {
    flex-basis: 50%;
    width: 100%;
}

.MoreInfo .container .innerLeft {
    margin-right: 1rem;
}

.MoreInfo .borderCountries {
    margin-top: 5%;
}

.MoreInfo .borderCountries button {
    margin: 0 1% 2% 0;
}


@media only screen and (max-width: 1080px) {
    .Main .MoreInfo .left {
        margin-top: 5vh;
    }

    .Main .MoreInfo .left,
    .Main .MoreInfo .right {
        flex-basis: 100%;
    }

    .Main .MoreInfo .right {
        padding: 4vh 0;
    }
}

@media only screen and (max-width: 650px) {
    .Main .container {
        flex-wrap: wrap;
    }


    .Main .MoreInfo .container .innerLeft,
    .Main .MoreInfo .container .innerRight {
        flex-basis: 100%;
        margin-bottom: 2vh;
    }

    .MoreInfo .borderCountries {
        margin-top: 9%;
    }

    .MoreInfo .borderCountries button {
        margin: 0 3% 3% 0;
    }
}

@media only screen and (min-width: 1500px) {
    .Main {
        padding: 20vh 7vw;
    }
}

@media only screen and (min-height: 810px) and (max-height: 880px) {
    .Main {
        padding-top: 18vh;
    }
}

@media only screen and (max-height: 900px) {
    .Main {
        padding-top: 20vh;
    }
}

@media only screen and (min-height: 1000px) {
    .Main {
        padding-top: 15vh;
    }
}