header {
    position: fixed;
    z-index: 10;
    right: 0;
    width: 100vw;
    padding: 3vh 10vw 3vh 7vw;
    display: flex;
    justify-content: space-between;
    font-weight: 800;
}

header h2,
header p,
header p i {
    font-size: 2.4rem;
}

header p {
    cursor: pointer;
}

header p i {
    padding-right: 0.6rem;
}

@media only screen and (max-width: 450px) {

    header h2,
    header p,
    header p i {
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 1050px) {
    header {
        padding-right: 7vw;
    }
}