.SearchBar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 5%;
    padding-left: 3rem;
    padding-right: 5%;
}

.SearchBar .input {
    position: relative;
    /* padding: 0 2%; */
    height: 5rem;
    width: 50%;
    border-radius: 10px;
}

.SearchBar .input i {
    position: absolute;
    top: 30%;
    left: 3%;
}

.SearchBar .input input {
    border-radius: 10px;
    outline: none;
    border: none;
    background-color: #333;
    color: #dedede;
    /* border: 5px solid red; */
    font-size: 1.6rem;
    /* margin-left: 3%; */
    padding: 3% 3% 3% 10%;
    /* height: 100%; */
    width: 100%;
}

.select-container {
    color: #dedede;
    font-size: 1.6rem;
    border-radius: 10px;
    width: 20%;
    position: relative;
}

.select-container .text-box {
    border-radius: 10px;
    padding: 7% 7%;
    background: #333333;
    cursor: pointer;
    height: 6rem;
    /* 	margin-bottom: 1%; */
}

.select-container p i {
    position: absolute;
    right: 0;
    margin-top: 2%;
    margin-right: 5%;
}

.select-container ul {
    background: #333;
    border-radius: 10px;
    margin-top: 1%;
    padding-left: 0;
    display: none;
    position: absolute;
    width: 100%;
}

.select-container ul li {
    list-style: none;
    line-height: 2;
    padding-left: 5%;
}

.select-container ul li:hover {
    cursor: pointer;
    background: khaki;
    color: #0f0f0f;
    width: 100%;
    border-radius: 10px;
}

@media only screen and (max-width: 450px) {
    .SearchBar .input {
        padding: 0;
        height: fit-content;
    }

    .SearchBar .input i {
        top: 30%;
    }

    .SearchBar .input input {
        padding: 1% 3% 1% 12%;
        margin-bottom: 10px;
    }

    .SearchBar .select-container {
        width: 70%;
    }

    .SearchBar .select-container .text-box {
        padding: 9% 7%;
    }
}

@media only screen and (min-width: 500px) and (max-width: 850px) {
    .SearchBar .input {
        padding: 0;
        height: fit-content;
    }

    .SearchBar .input i {
        top: 40%;
    }

    .SearchBar .input input {
        padding: 0 3% 0 8%;
        width: 100%;
    }

    .SearchBar .select-container {
        width: 50%;
    }
}

@media only screen and (max-width: 850px) {
    .SearchBar {
        flex-flow: column wrap;
    }

    .SearchBar .input {
        margin-bottom: 5vh;
        width: 100%;
        padding: 5%;
    }
}

@media only screen and (min-width: 760px) and (max-width: 850px) {


    .SearchBar .select-container .text-box {
        padding: 6% 7%;
    }

    .SearchBar .select-container p i {
        margin-top: 1%;
    }
}

@media only screen and (min-width: 1000px) and (max-width: 1300px) {
    .SearchBar .input input {
        padding: 4% 3% 3% 13%;
    }

    .SearchBar .select-container .text-box {
        padding: 9.5% 7%;
    }
}