.Country {
    width: 18vw;
    /* height: fit-content; */
    display: flex;
    justify-content: center;
    vertical-align: middle;
    flex-direction: column;
    /* flex-grow: 1; */
    /* flex-shrink: 1; */
    border-radius: 5px;
    height: 45rem;
    text-decoration: none;
}

.Country .image,
.Country .Country-text {
    height: 50%;
    flex: 1 1;
}

.Country .image {
    max-width: 100%;
    width: 100%;
    /* height: auto; */
}

.Country .image img {
    width: 100%;
    min-height: auto;
    height: 100%;
    max-height: auto;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.Country .Country-text {
    padding: 3rem 3rem 0;
}

.Country .Country-text h2 {
    font-weight: 800;
    font-size: 2.2rem;
    padding-bottom: 8%;
}

.Country .Country-text p,
.Country .Country-text p span {
    padding-bottom: 3%;
    font-size: 1.5rem;
}

.Country .Country-text p {
    font-weight: 300;
}

.Country .Country-text p span {
    font-weight: 600;
    margin-right: 3%;
}

@media only screen and (max-width: 400px) {
    .Country {
        height: 35rem;
    }
}

@media only screen and (max-width: 500px) {
    .Country {
        width: 100%;
        height: 40rem;
    }
}


@media only screen and (min-width: 550px) {
    .Country {
        width: 100%;
        height: 43rem;
    }
}


@media only screen and (min-width: 700px) {
    .Country {
        width: 36vw;
    }
}

@media only screen and (min-width: 1000px) {
    .Country {
        width: 23.5vw;
    }
}

@media only screen and (min-width: 1200px) {
    .Country {
        width: 25vw;
    }
}

@media only screen and (min-width: 1350px) {
    .Country {
        width: 18vw;
    }
}