.CountryList {
    padding: 19vh 5vw 5vh;
    width: 100vw;
    height: 100%;
}

.CountryList .List-section {
    display: flex;
    flex-flow: row wrap;
    padding: 5px;
}


@media only screen and (min-height: 810px) and (max-height: 880px) {
    .CountryList {
        padding-top: 18vh;
    }
}

@media only screen and (min-height: 1000px) {
    .CountryList {
        padding-top: 15vh;
    }
}